'use strict';

var app = require('../module');

app.controller('MenuController', function($scope, $state, foodService) {
	$scope.foodService = foodService;
	$scope.range = function(n) {
        return new Array(n);
    };
	if(!$scope.foodService.selected) {
		$state.go('home');
	}
});

module.exports = app;


