'use strict';

var app = require('../module');

app.controller('CheckoutController', function($scope, $state, foodService) {
	$scope.checkoutStep = 1;
	$scope.foodService = foodService;
	$scope.range = function(n) {
        return new Array(n);
    };

    if(!$scope.foodService.selected) {
		$state.go('home');
	}

});

module.exports = app;


