'use strict';

var app = require('./module');
var home = require('./home');
var places = require('./places');
var menu = require('./menu');
var checkout = require('./checkout');
var complete = require('./complete');
var foodService = require('./home/food-service');

app.addModules([
  'ui.router'
]);

app.TEMPLATES = {
  HOME: require('./home/template.html'),
  PLACES: require('./places/template.html'),
  MENU: require('./menu/template.html'),
  CHECKOUT: require('./checkout/template.html'),
  COMPLETE: require('./complete/template.html')
};

app.config(function($stateProvider, $urlRouterProvider) {

  $stateProvider
    .state('home', {
      url: '/',
      template: app.TEMPLATES.HOME,
      controller: 'HomeController'
    })
    .state('places', {
      url: '/places',
      template: app.TEMPLATES.PLACES,
      controller: 'PlacesController'
    })
    .state('menu', {
      url: '/menu',
      template: app.TEMPLATES.MENU,
      controller: 'MenuController'
    })
    .state('checkout', {
      url: '/checkout',
      template: app.TEMPLATES.CHECKOUT,
      controller: 'CheckoutController'
    })
    .state('complete', {
      url: '/complete',
      template: app.TEMPLATES.COMPLETE,
      controller: 'CompleteController'
    });

  $urlRouterProvider.otherwise('/');

});

app.controller('NavigationController', function($scope, foodService) {
  $scope.now = moment();
  $scope.foodService = foodService;
});

module.exports = app;


