'use strict';

var app = require('../module');

app.controller('CompleteController', function($scope, $state, foodService) {
	$scope.rating = 0;
	$scope.foodService = foodService;
	$scope.foodService.cart = [];
	$scope.range = function(n) {
        return new Array(n);
    };
    if(!$scope.foodService.selected) {
		$state.go('home');
	}

});

module.exports = app;


